import React, { useState, useEffect, useCallback } from 'react';
import { Button } from './Button';
import { Alert, AlertDescription, AlertTitle } from './Alert';
import './PerformAction.css';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

const PerformAction = ({ session, action }) => {
  const [status, setStatus] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [plotStatus, setPlotStatus] = useState([]);
  const [selectedPlotId, setSelectedPlotId] = useState('');

  const fetchPlots = useCallback(async () => {
    if (!session?.actor) {
      setStatus('Error: Please login first');
      return;
    }

    try {
      const response = await fetch(`${API_BASE_URL}/plots/${session.actor}`);
      const result = await response.json();

      setPlotStatus(result.plots || []);
    } catch (error) {
      console.error('Error fetching plot status:', error);
    }
  }, [session]);

  useEffect(() => {
    fetchPlots();
  }, [fetchPlots]);

  const performAction = async () => {
    if (!session?.actor || !selectedPlotId) {
      setStatus('Error: Please login first or select a plot');
      return;
    }

    setIsLoading(true);
    setStatus('');

    try {
      const actionData = {
        action: action,
        user: session.actor,
        plot_id: parseInt(selectedPlotId, 10),
      };

      await session.transact({
        actions: [{
          account: process.env.REACT_APP_CONTRACT_NAME,
          name: action,
          authorization: [{
            actor: session.actor,
            permission: 'active',
          }],
          data: actionData,
        }],
      }, {
        blocksBehind: 3,
        expireSeconds: 30,
      });

      setStatus(`${action} action performed successfully`);
      fetchPlots();
    } catch (error) {
      console.error('Error performing action:', error);
      setStatus(`Error performing ${action} action: ${error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="perform-action">
      <h3>{action}</h3>
      {status && <Alert type="error"><AlertTitle>Error</AlertTitle><AlertDescription>{status}</AlertDescription></Alert>}
      <select onChange={(e) => setSelectedPlotId(e.target.value)} value={selectedPlotId}>
        <option value="">Select Plot</option>
        {plotStatus.map(plot => (
          <option key={plot.plot_id} value={plot.plot_id}>
            Plot ID: {plot.plot_id}, User: {plot.user}
          </option>
        ))}
      </select>
      <Button onClick={performAction} isLoading={isLoading}>
        {isLoading ? 'Processing...' : `Perform ${action}`}
      </Button>
    </div>
  );
};

export default PerformAction;
