import { SessionKit, ChainDefinition } from '@wharfkit/session';
import { WebRenderer } from '@wharfkit/web-renderer';
import { WalletPluginAnchor } from '@wharfkit/wallet-plugin-anchor';
import { WalletPluginCloudWallet } from '@wharfkit/wallet-plugin-cloudwallet';

let sessionKit = null;

const initializeSessionKit = () => {
    try {
        console.log('Initializing SessionKit...');

        const chainId = process.env.REACT_APP_CHAINID;
        const rpcUrl = process.env.REACT_APP_RPC;
        const appName = process.env.REACT_APP_SITE_TITLE;

        if (!chainId || !rpcUrl) {
            throw new Error('Chain ID or RPC URL is missing');
        }

        const chain = new ChainDefinition({
            id: chainId,
            url: rpcUrl,
        });

        console.log('Chain definition:', chain);

        sessionKit = new SessionKit({
            appName,
            chains: [chain],
            ui: new WebRenderer(),
            walletPlugins: [
                new WalletPluginAnchor(),
                new WalletPluginCloudWallet(),
            ],
        });

        console.log('SessionKit initialized successfully:', sessionKit);
        return sessionKit;
    } catch (error) {
        console.error('Error initializing SessionKit:', error);
        throw error;
    }
};

export { initializeSessionKit };
export default sessionKit;
