import React from 'react';
import '../styles.css'; // Ensure the correct path to styles.css

const Login = ({ session, login, logout, isLoading, isSessionKitReady }) => {
  return (
    <div className="login-container">
      {session ? (
        <button className="green-button" onClick={logout} disabled={isLoading}>
          LOGOUT
        </button>
      ) : (
        <button 
          className="green-button" 
          onClick={login} 
          disabled={isLoading || !isSessionKitReady}
        >
          {isLoading ? 'LOADING...' : 'LOGIN'}
        </button>
      )}
    </div>
  );
};

export default Login;
