import React, { useState, useEffect, useCallback, useMemo } from 'react';
import './App.css';
import sessionKit from './sessionConfig';
import ErrorBoundary from './components/ErrorBoundary';
import logo from './MaestroBeatzLogo.png';
import AccountInfo from './components/AccountInfo';
import PerformAction from './components/PerformAction';
import FarmersList from './components/FarmersList';
import Login from './components/Login';
import NFTList from './components/NFTList';
import Farms from './components/Farms';
import Modal from './components/Modal';
import { getFarmers, registerFarmer, unregisterFarmer } from './components/api';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:3000/api';
const SITE_TITLE = process.env.REACT_APP_SITE_TITLE || 'Tomato Farm Game';

const actions = ['plantseeds', 'waterplants', 'harvest', 'sellcrops'];

function App() {
    const [session, setSession] = useState(null);
    const [accountInfo, setAccountInfo] = useState(null);
    const [farmers, setFarmers] = useState([]);
    const [isRegistered, setIsRegistered] = useState(false);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [isModalOpen, setIsModalOpen] = useState(false);

    useEffect(() => {
        const initializeSession = async () => {
            try {
                console.log('Initializing Session...');
                const restoredSession = await sessionKit.restore();
                if (restoredSession) {
                    console.log('Session restored:', restoredSession);
                    setSession(restoredSession);
                    await fetchData(restoredSession);
                }
            } catch (e) {
                console.error('Error during session initialization:', e);
                setError('Error during session initialization: ' + e.message);
            }
        };
        initializeSession();
    }, []);

    const fetchData = useCallback(async (session) => {
        if (!session || !session.actor) return;
        setIsLoading(true);
        try {
            console.log('Fetching data for session:', session);
            const [farmersData, accountInfoData] = await Promise.all([
                getFarmers(),
                fetch(`${API_BASE_URL}/account/${session.actor.toString()}`).then(res => res.json())
            ]);
            
            setFarmers(farmersData.farmers || []);
            setAccountInfo(accountInfoData);
            console.log('Account Info:', accountInfoData);

            const isUserRegistered = farmersData.farmers.some(farmer => farmer.accountName === session.actor.toString());
            setIsRegistered(isUserRegistered);

            setError(null);
        } catch (e) {
            console.error('Failed to fetch data:', e);
            setError('Failed to fetch data: ' + e.message);
        } finally {
            setIsLoading(false);
        }
    }, []);

    const handleLogin = async () => {
        setIsLoading(true);
        setError(null);
        try {
            console.log('Attempting to login...');
            const { session: newSession } = await sessionKit.login();
            console.log('Login response:', newSession);
            if (newSession) {
                setSession(newSession);
                await fetchData(newSession);
            } else {
                setError('Login failed: No session returned');
            }
        } catch (e) {
            console.error('Error during login:', e);
            setError('Error during login: ' + e.message);
        } finally {
            setIsLoading(false);
        }
    };

    const handleLogout = async () => {
        if (session) {
            try {
                await session.logout();
                setSession(null);
                setAccountInfo(null);
                setFarmers([]);
                setIsRegistered(false);
            } catch (e) {
                console.error('Error during logout:', e);
                setError('Error during logout: ' + e.message);
            }
        }
    };

    const handleRegisterFarmer = async () => {
        if (!session || !session.actor) {
            setError('Please login first');
            return;
        }
        try {
            if (isRegistered) {
                await unregisterFarmer(session.actor.toString());
                setIsRegistered(false);
            } else {
                const nickname = prompt("Enter a nickname (max 12 characters):");
                if (!nickname || nickname.length > 12) {
                    setError('Nickname is required and must be 12 characters or less.');
                    return;
                }
                await registerFarmer(session.actor.toString(), nickname);
                setIsRegistered(true);
            }
            await fetchData(session);
        } catch (e) {
            console.error(`Failed to ${isRegistered ? 'unregister' : 'register'} farmer:`, e);
            setError(`Failed to ${isRegistered ? 'unregister' : 'register'} farmer: ` + e.message);
        }
    };

    const performTransaction = useCallback(async (actionData) => {
        if (!session) {
            console.error('No active session');
            setError('No active session. Please login first.');
            return;
        }
        try {
            const result = await session.transact({ action: actionData });
            console.log('Transaction result:', result);
            return result;
        } catch (error) {
            console.error('Transaction error:', error);
            setError('Transaction error: ' + error.message);
        }
    }, [session]);

    const toggleModal = useCallback(() => {
        setIsModalOpen(prev => !prev);
    }, []);

    const memoizedActions = useMemo(() => actions.map(action => (
        <PerformAction
            key={action}
            session={session}
            action={action}
            plotId={1}
            performTransaction={performTransaction}
        />
    )), [session, performTransaction]);

    return (
        <ErrorBoundary>
            <div className="App">
                <header className="App-header">
                    <h1>Welcome to {SITE_TITLE}</h1>
                    <img src={logo} className="App-logo" alt="logo" />
                    <Login 
                        session={session} 
                        login={handleLogin} 
                        logout={handleLogout} 
                        isLoading={isLoading}
                    />
                    {isLoading && <p>Loading...</p>}
                    {error && <p className="error-message">{error}</p>}
                    {session && accountInfo && (
                        <>
                            <div className="section">
                                <h2>Account Information</h2>
                                <AccountInfo accountInfo={accountInfo} />
                            </div>
                            <div className="button-group">
                                <button onClick={handleRegisterFarmer}>
                                    {isRegistered ? 'Unregister' : 'Register as Farmer'}
                                </button>
                                <button onClick={toggleModal}>Show Registered Farmers</button>
                            </div>
                            <div className="section">
                                <h2>Perform Actions</h2>
                                <div className="actions-grid">
                                    {memoizedActions}
                                </div>
                            </div>
                            <div className="section">
                                <h2>Farms</h2>
                                <Farms session={session} />
                            </div>
                            <div className="section">
                                <h2>Your NFTs</h2>
                                <NFTList actor={session.actor.toString()} />
                            </div>
                        </>
                    )}
                </header>
                {isModalOpen && (
                    <Modal onClose={toggleModal}>
                        <h2>Registered Farmers</h2>
                        <FarmersList farmers={farmers} />
                    </Modal>
                )}
            </div>
        </ErrorBoundary>
    );
}

export default App;
